.app {
  display: flex;
  padding: 32px;
}

.app__sidebar {
  width: 25%;
  flex-grow: 0;
}

.app__content {
  flex-grow: 1;
  padding: 0 32px;
}

.app__sidebar__file {
  width: 100%;
  display: block;
  background: white;
  border-radius: 2px;
  border: 2px solid #888;
  box-shadow: none;
  padding: 8px;
  margin-top: 16px;
  font-size: inherit;
  overflow-y: hidden;
}

.app__sidebar__file:active {
  outline: none;
}

.app__sidebar__file--active {
  background: #ddd;
}

.app__sidebar__file:hover {
  cursor: pointer;
  background: #efefef;
}

.file__name {
  margin: 0 0 16px 0;
  padding: 0 8px;
}

.file__distances {
  display: flex;
}

.distance {
  width: 100%;
  padding: 0 8px;
}

.distance__header {
  margin: 0 0 8px 0;
}

.distance__table {
  width: 100%;
  border-collapse: collapse;
}

.distance__table th,
.distance__table td {
  padding: 4px 8px;
  border: none;
}

.distance__table td,
.distance__table th {
  text-align: left;
}

.distance__table tbody tr:nth-child(odd) {
  background-color: #efefef;
}

.distance__table tbody td {
  font-family: monospace;
}

.ralign {
  text-align: right !important;
}
